import React, { useState, useEffect } from "react";
import "./DetailToko.scss";
import "../Table.scss";
import { withStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import TablePagination from "@material-ui/core/TablePagination";
import { Button, Avatar, Form, InputNumber } from "antd";
import { UserOutlined } from "@ant-design/icons";
import { useSelector, useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";

const Index = (props) => {
  const history = useHistory();
  let coba = useSelector((state) => state.formValue);
  const dispatch = useDispatch();

  const data = props.data;
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [boughtItems, setBoughtItems] = useState([]);
  const [loading, setLoading] = useState(false);
  const [bankName, setbankName] = useState(props.bankName);
  const bankDetail = props.bankDetail;

  const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: "#1DC6C6",
      color: theme.palette.common.white,
      fontSize: 22,
    },
    body: {
      fontSize: 18,
    },
  }))(TableCell);

  function handleChangePage(event, newPage) {
    setPage(newPage);
  }

  function handleChangeRowsPerPage(event) {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  }

  async function finish(value) {
    console.log(value)
    setBoughtItems(null);
    setLoading(true);
    let no = 0;
    for (let i = 0; i < data.length; i++) {
      if (value[i+1] != null || value[i+1] != undefined || value[i+1] > 0) {
        data[i].inputdata = value[i+1];
        boughtItems.push(data[i]);
      }
    }
    console.log("bought");
    console.log(boughtItems);
    dispatch({ type: "ADD_DATA", payload: boughtItems, acc:props.bankName, detail:props.bankDetail });
    history.push(props.link);
    setLoading(false);
  }

  return (
    <div id="detailtoko">
      <Form onFinish={finish} initialValues={coba}>
        <div className="namatoko">
          <div style={{ display: "inline" }}>
            <Avatar
              size={75}
              icon={
                props.gambar == null ? (
                  <UserOutlined />
                ) : (
                  <img src={props.gambar} />
                )
              }
            />
          </div>
          <div style={{ display: "inline", marginLeft: "2%" }}>
            Toko {props.nama}
          </div>
        </div>

        <div className="tablestok">
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <StyledTableCell align="left">Nama Produk</StyledTableCell>
                  <StyledTableCell align="center">Grade</StyledTableCell>
                  <StyledTableCell align="center">Stok</StyledTableCell>
                  <StyledTableCell align="center">Harga</StyledTableCell>
                  <StyledTableCell align="center">Beli</StyledTableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {(rowsPerPage > 0
                  ? data.slice(
                      page * rowsPerPage,
                      page * rowsPerPage + rowsPerPage
                    )
                  : data
                ).map((row, index) => (
                  <TableRow key={row}>
                    <StyledTableCell align="left">{row.item}</StyledTableCell>
                    <StyledTableCell align="center">
                      {row.grade}
                    </StyledTableCell>
                    <StyledTableCell align="center">{row.qty}</StyledTableCell>
                    <StyledTableCell align="center">
                      Rp. {props.store === "baku" ? row.price : row.sellPrice}
                    </StyledTableCell>
                    <StyledTableCell align="center">
                      <Form.Item name={row.id}>
                        <InputNumber
                          style={{ display: "inline-block", width: 100 }}
                          placeholder="ex: 100"
                          min={1}
                        />
                      </Form.Item>
                    </StyledTableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
          <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={data.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onChangePage={handleChangePage}
            onChangeRowsPerPage={handleChangeRowsPerPage}
          />
        </div>
        <div className="buttongroupbeli">
          <Button className="btn_secondary">Kembali</Button>
          <Button className="btn_primary" htmlType="submit" loading={loading}>
            Selanjutnya
          </Button>
        </div>
      </Form>
    </div>
  );
};

export default Index;
